}

a {
  font-size: 1.2rem;
  font-weight: 300;
  color: var(--color-white);
  text-decoration: none;
  margin-right: 1rem;
}
a:hover {
  color: orangered;
}
a.btn {
  border-radius: 5px;
  /* margin-left: 1rem; */
  font-weight: 500;
  padding: 5px 8px;
}
a.btn-dark {
  border: 2px solid var(--color-white);
  background: transparent;
}
a.btn-orange {
  border: 2px solid transparent;
  background: orangered;
  color: var(--color-white);
}

#header {
  position: relative;
}
.header {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.header h1 {
  font-size: 4rem;
  font-weight: 700;
  line-height: 1.4;
  text-transform: uppercase;
  color: var(--color-white);
  position: relative;
  padding-bottom: 2rem;
}
.header h1 span {
  display: block;
}
h1 span:nth-child(2),
h1 span:nth-child(3) {
  font-weight: 100;
  font-size: 3rem;
  text-transform:lowercase;
}
.header h1::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 80%;
  height: 1px;
  background: var(--color-white);
}
.header-left p {
  margin: 3rem 0;
  width: 80%;
}
.header-right {
  width: 50%;
}
.floating-icon {
  width: 5rem;
  height: 5rem;
  background: var(--light-blue);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  cursor: pointer;
}

.floating-icon a {
  border: 1px solid #fff;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.mouse {
  -webkit-transform: translateY(-25%);
          transform: translateY(-25%);
  -webkit-animation: bounce 0.8s ease infinite alternate;
          animation: bounce 0.8s ease infinite alternate;
}
@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
  }
}
@keyframes bounce {
  0% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
  }
}
@media screen and (max-width: 769px) {
  .header h1 {
    font-size: 3rem;
  }
}
@media screen and (max-width: 600px) {
  .header {
    flex-direction: column;
  }
  .header-left,
  .header-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
  }
  .header-left {
    margin-bottom: 4rem;
  }
  .header h1::after {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .header-right {
    width: 40%;
  }
}

.navbar {
