  display: none;
}
.nav-btn {
  width: 20%;
}
@media screen and (max-width: 769px) {
  .nav-btn {
    display: none;
  }
}

@media screen and (max-width: 600px) {
  .nav-btn {
    display: block;
  }
  .nav-links {
    display: none;
    border: 2px solid var(--color-white);
    /* z-index: 111; */
  }
  .navbar .menu-icons {
    display: block;
  }
  .navbar #nav-links-mobile,
  .navbar #nav-links-mobile-hide {
    display: block;
    position: absolute;
    left: 0;
    top: 8rem;
    width: 100%;
    background: var(--dark-blue);
    border-top: 1px solid var(--color-white);
    padding-bottom: 5px;
    transition: all 0.3s;
  }
  .navbar #nav-links-mobile {
    -webkit-animation: slide-in 0.5s ease-in-out;
            animation: slide-in 0.5s ease-in-out;
  }
  @-webkit-keyframes slide-in {
    0% {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  @keyframes slide-in {
    0% {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  .navbar #nav-links-mobile-hide {
    -webkit-animation: slide-out 0.5s ease-in-out;
            animation: slide-out 0.5s ease-in-out;
    left: -100%;
  }
  @-webkit-keyframes slide-out {
    0% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
    100% {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
  }
  @keyframes slide-out {
    0% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
    100% {
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
  }
  .navbar #nav-links-mobile li,
  .navbar #nav-links-mobile-hide li {
    text-align: center;
    padding: 1rem 0;
    border-bottom: 1px solid var(--color-white);
    margin: 0 3rem;
  }
  .navbar #nav-links-mobile li:last-child {
    border-bottom: none;
  }
}

